@import url(https://fonts.googleapis.com/css?family=Pangolin);
body {
  margin: 0;
  font-size: large;
  /* font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.blink_me {
  animation: blinker 3s linear infinite;
}

@keyframes blinker {
  50% {
    opacity: 0;
  }
}

.cursor {
  display: inline-block;
  width: 10px;
  height: 1rem;
  background: #ffffff;
  margin-left: 5px;
  border-radius: 1px;
  animation: blinker 2s linear infinite;
}
.source{width:100%;height:90vh;left:0;top:0;position:relative;display:none}.source .source-block{position:absolute;width:100%;height:100%;left:0;top:0;z-index:301;overflow:scroll}.source .source-block pre{display:block;position:relative}.source .source-block pre code{font-size:90%}.demo-block{border-top:solid 1px #dcdcdc;border-bottom:solid 1px #dcdcdc}.html-book{background-size:cover}.flip-book{box-shadow:0 0 20px 0 rgba(0,0,0,0.5);display:none;background-size:cover}.page{background-color:#fdfaf7;color:#785e3a;border:solid 1px #c2b5a3;overflow:hidden}.page .page-content{width:100%;height:100%;display:flex;flex-direction:column;justify-content:space-between;align-items:stretch}.page .page-content .page-header{height:30px;font-size:100%;text-transform:uppercase;text-align:center}.page .page-content .page-image{height:100%;background-size:contain;background-position:center center;background-repeat:no-repeat}.page .page-content .page-text{height:100%;flex-grow:1;font-size:80%;text-align:justify;margin-top:10px;padding-top:10px;box-sizing:border-box;border-top:solid 1px #f4e8d7}.page .page-content .page-footer{height:30px;border-top:solid 1px #f4e8d7;font-size:80%;color:#998466}.page.--left{border-right:0;box-shadow:inset -7px 0 30px -7px rgba(0,0,0,0.4)}.page.--right{border-left:0;box-shadow:inset 7px 0 30px -7px rgba(0,0,0,0.4)}.page.--right .page-footer{text-align:right}.page.hard{background-color:#f2e8d9;border:solid 1px #998466}.page.page-cover{background-color:#0c0b0a;color:#fff;border:solid 1px #998466}.page.page-cover h2{text-align:center;padding-top:50%;font-size:210%}.page.page-cover.page-cover-top{box-shadow:inset 0px 0 30px 0px rgba(36,10,3,0.5),-2px 0 5px 2px rgba(0,0,0,0.4)}.page.page-cover.page-cover-bottom{box-shadow:inset 0px 0 30px 0px rgba(36,10,3,0.5),10px 0 8px 0px rgba(0,0,0,0.4)}

